/* ========================================================================
 * DOM-based Routing
 * Based on http://goo.gl/EUTi53 by Paul Irish
 *
 * Only fires on body classes that match. If a body class contains a dash,
 * replace the dash with an underscore when adding it to the object below.
 *
 * .noConflict()
 * The routing is enclosed within an anonymous function so that you can
 * always reference jQuery with $, even when in .noConflict() mode.
 * ======================================================================== */

(function($) {

  // Use this variable to set up the common and page specific functions. If you
  // rename this variable, you will also need to rename the namespace below.
  var Sage = {
    // All pages
    'common': {
      init: function() {
        // JavaScript to be fired on all pages
        function calcSize() {
          console.log("bravoh");
          var heightScreen = $(window).height();
          var widthScreen = $(window).width();
          var heightHomeSection = $("section.home .home_content").outerHeight();
          var heightHeader = $("header").outerHeight();
          var heightHomeHero = $(".partial_hero").outerHeight();
          var heroStoryHeight = $('.hero_content').outerHeight();  
          var heroStoryWidth = $('.hero_content').outerWidth();
          $('.partial_hero').css({ 'height': heightScreen });
          $('.story_hero').css({ 'height': heightScreen });
          $('.hero_content').css({ 'margin-top' : heightScreen/2  - heroStoryHeight/2 });
          //$('.hero_content').css({ 'margin-left' : widthScreen/2  - heroStoryWidth/2 });
          $('.button-nav').css({'top' : (heightScreen/2) - 20  })
          $('.square').each(function() {
            $(this).css({ 'height': $(this).outerWidth() });
          });
        }

        $(window).resize(function() {
          calcSize();
        });

        calcSize();

        $('.tabs').herotabs();
        $('.header').stickyNavbar();

        $('.menu_button.search').on('click', function() {
          $('.partial_filters').toggleClass('visible');
        });


        // font families
        WebFontConfig = {
            google: { families: [ 'Noto+Serif::latin', 'Open+Sans:400,700:latin' ] }
          };
          (function() {
            var wf = document.createElement('script');
            wf.src = ('https:' == document.location.protocol ? 'https' : 'http') +
              '://ajax.googleapis.com/ajax/libs/webfont/1/webfont.js';
            wf.type = 'text/javascript';
            wf.async = 'true';
            var s = document.getElementsByTagName('script')[0];
            s.parentNode.insertBefore(wf, s);
          })();






      },
      finalize: function() {
        // JavaScript to be fired on all pages, after page specific JS is fired
      }
    },
    // Home page
    'home': {
      init: function() {
        // JavaScript to be fired on the home page
      },
      finalize: function() {
        // JavaScript to be fired on the home page, after the init JS
      }
    },
    // About us page, note the change from about-us to about_us.
    'about_us': {
      init: function() {
        // JavaScript to be fired on the about us page
      }
    }
  };

  // The routing fires all common scripts, followed by the page specific scripts.
  // Add additional events for more control over timing e.g. a finalize event
  var UTIL = {
    fire: function(func, funcname, args) {
      var fire;
      var namespace = Sage;
      funcname = (funcname === undefined) ? 'init' : funcname;
      fire = func !== '';
      fire = fire && namespace[func];
      fire = fire && typeof namespace[func][funcname] === 'function';

      if (fire) {
        namespace[func][funcname](args);
      }
    },
    loadEvents: function() {
      // Fire common init JS
      UTIL.fire('common');

      // Fire page-specific init JS, and then finalize JS
      $.each(document.body.className.replace(/-/g, '_').split(/\s+/), function(i, classnm) {
        UTIL.fire(classnm);
        UTIL.fire(classnm, 'finalize');
      });

      // Fire common finalize JS
      UTIL.fire('common', 'finalize');
    }
  };

  // Load Events
  $(document).ready(UTIL.loadEvents);

})(jQuery); // Fully reference jQuery after this point.

